import styled from "styled-components";
import { Text, Title3, Title4 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";
import Proffesional from "../../assets/images/professional.svg";
import { css } from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";

const SVerticalBox = styled.div`
  padding: 40px;
  border-radius: 53px;
  grid-column: 1 2;
  max-width: 790px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 27px;

  background: ${({ color }) => {
    switch (color) {
      case "blue":
        return `radial-gradient(
      42.66% 39.97% at 92.66% 60.03%,
      rgba(49, 189, 255, 0.5) 0%,
      rgba(49, 189, 255, 0) 100%
    ),
    radial-gradient(
      40.9% 40.9% at 0% 28.67%,
      #12b1ff 0%,
      rgba(18, 177, 255, 0) 100%
    ),
    #2d84f7`;
      case "green":
        return `radial-gradient(
            70.06% 60.37% at 120.06% 47.76%, 
            #20A8E9 0%, rgba(32, 168, 233, 0) 100%), 
        radial-gradient(
            90.44% 52.83% at 11.84% 48.62%, 
            #1C97D3 0%, rgba(28, 151, 211, 0) 100%), 
        #2DDFF7;`;
    }
  }};
`;

const ProductView = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: top;
  height: 542px;
  padding: 24px;

  background: radial-gradient(
    150.56% 139.78% at -35.42% -28.77%,
    rgba(255, 255, 255, 0.1628) 0%,
    rgba(255, 255, 255, 0.286) 100%
  );
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(13.5px);

  border-radius: 24px;

  & div {
    width: 12px;
    height: 12px;
    margin-right: 17px;
    background: #ffffff;
    opacity: 0.6;
    border-radius: 100%;
  }
`;

const Desc = styled.div`
  margin: 33px 0;
  min-height: 321px;
  display: flex;
  flex-direction: column;
  gap: 40px;
`;

const Name = styled(Title3)`
  & span {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    padding: 13px 24px;
    gap: 8px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 38px;
    margin-left: 24px;
    cursor: pointer;
  }
`;

const textStyle = css`
  font-weight: 300;
  font-size: 20px;
  line-height: 150%;
`;

const List = styled.ul`
  padding-left: 30px;
  max-width: 530px;
  color: white;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;

  & li {
    position: relative;
    ${textStyle}
  }

  & li::before {
    content: "";
    background-color: #ffffff55; /* Цвет маркера */
    width: 10px;
    height: 10px;
    position: absolute;

    top: 10px;
    left: -26px;
    border-radius: 100%;
  }
`;

const Placeholder = styled.div`
  height: 35px;
`;

const ProffesionalText = styled(Text)`
  display: flex;
  gap: 15px;
  align-items: center;

  ${textStyle}

  & img {
    aspect-ratio: 28 / 28;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  gap: 10px;

  & a {
    width: 100%;
  }
`;

function VerticalBox({
  boxcolor,
  btncolor,
  name,
  proffesional,
  title,
  list,
  socials,
  href,
  language,
}) {
  return (
    <SVerticalBox color={boxcolor}>
      <Title4>{title}</Title4>
      {proffesional ? (
        <ProffesionalText>
          <img width="28" height="28" loading="lazy" src={Proffesional} />
          {language === "Ru"
            ? "Для профессионалов рынка"
            : EngLoc.products.products_boxes[0].proffessionals}
        </ProffesionalText>
      ) : (
        <Placeholder />
      )}
      <ProductView>
        <div /> <div /> <div />
      </ProductView>
      <Desc>
        <Name>
          {name}
          <span>
            {socials &&
              socials.map((social) => (
                <img
                  width="28"
                  height="28"
                  key={Math.random()}
                  loading="lazy"
                  src={social}
                />
              ))}
          </span>
        </Name>
        <List>
          {list &&
            list.map((list_item) => <li key={Math.random()}>{list_item}</li>)}
        </List>
      </Desc>
      <ButtonBox>
        <a href={href}>
          <Button type="secondary" color={btncolor}>
            {language === "Ru" ? "Войти" : EngLoc.buttons.login}
          </Button>
        </a>
        <a href={href}>
          <Button type="secondary" link={true}>
            {language === "Ru" ? "Подробнее" : EngLoc.buttons.more}
          </Button>
        </a>
      </ButtonBox>
    </SVerticalBox>
  );
}

export default VerticalBox;
