import styled from "styled-components";

import { Title2 } from "../../assets/styles/component";
import VerticalBox from "./verticalBoxDesktop";
import HorizontalBox from "./horizontalBox";

import Youtube from "../../assets/images/youtube.svg";
import Odnoklassniki from "../../assets/images/odnoklassniki.svg";
import Vk from "../../assets/images/vk.svg";
import Telegram from "../../assets/images/telegram.svg";
import Instagram from "../../assets/images/instagram.svg";
import Yandex_Zen from "../../assets/images/Yandex_Zen.svg";
import Play from "../../assets/images/Play.svg";
import { EngLoc } from "../../assets/constants/localizations";
import {
  smooth_right,
  smooth_left,
  smooth_bottom,
  smooth_up,
} from "../../assets/styles/animations";
import { useEffect, useRef } from "react";
import { useState } from "react";

const SProducts = styled.section`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-items: center;
  margin: 0 15%;
  padding: 150px 0;

  @media (max-width: 1500px) {
    margin: 0 5%;
  }

  & > div {
    opacity: 0;
  }
  
  & div[color="blue"] {
    ${({ view }) => !view && smooth_left}
  }

  & div[color="green"] {
    ${({ view }) => !view && smooth_left}
  }

  & div[color="violet"] {
    ${({ view }) => !view && smooth_left}
  }

  & div[color="pink"] {
    ${({ view }) => !view && smooth_left}
  }
`;

const HightTitle = styled(Title2)`
  grid-column: 1/3;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  padding-bottom: 120px;
`;

const Products = ({ language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  return (
    <SProducts ref={elem} view={view}>
      <HightTitle>
        {language === "Ru" ? "Наши продукты" : EngLoc.products.title}
      </HightTitle>
      <VerticalBox
        language={language}
        boxcolor="blue"
        btncolor="#0968E0"
        title={
          language === "Ru"
            ? "Крупнейшая аналитическая и рекламная платформа для маркетинга и бизнеса"
            : EngLoc.products.products_boxes[0].title
        }
        proffesional={true}
        name="SocialData"
        list={[
          language === "Ru"
            ? "Более 300 000 источников во всех социальных сетях и мессенджерах"
            : EngLoc.products.products_boxes[0].desc_list[0],
          language === "Ru"
            ? "Глубокая аналитика по более 15 параметрам"
            : EngLoc.products.products_boxes[0].desc_list[1],
          language === "Ru"
            ? "Запросы на подборки и готовые подборки"
            : EngLoc.products.products_boxes[0].desc_list[2],
          language === "Ru"
            ? "Запуск РК в несколько кликов"
            : EngLoc.products.products_boxes[0].desc_list[3],
        ]}
        socials={[Telegram, Vk, Instagram, Youtube, Odnoklassniki, Yandex_Zen]}
        href="https://data.socialjet.pro/"
      />
      <VerticalBox
        language={language}
        boxcolor="green"
        btncolor="#00A9C6"
        title={
          language === "Ru"
            ? "Крупнейшая аналитическая и рекламная платформа для маркетинга и бизнеса"
            : EngLoc.products.products_boxes[1].title
        }
        name="TeleJet"
        list={[
          language === "Ru"
            ? "Запуск официальной рекламы в TG ADS"
            : EngLoc.products.products_boxes[1].desc_list[0],
          language === "Ru"
            ? "Размещение в любых каналах телеграм"
            : EngLoc.products.products_boxes[1].desc_list[1],
          language === "Ru"
            ? "Более 10 000 подключенных паблишеров"
            : EngLoc.products.products_boxes[1].desc_list[2],
          language === "Ru"
            ? "Полная аналитика рекламной кампании"
            : EngLoc.products.products_boxes[1].desc_list[3],
        ]}
        socials={[Telegram]}
        href="https://socialjet.pro/telegram/"
      />
      <HorizontalBox
        language={language}
        boxcolor="pink"
        btncolor="#944DCD"
        title={
          language === "Ru"
            ? "Платформа для размещения видео-рекламы на новостных и контентных сайтах"
            : EngLoc.products.products_boxes[2].title
        }
        name="RocketVideo"
        list={[
          language === "Ru"
            ? "Более 300 000 источников во всех социальных сетях и мессенджерах"
            : EngLoc.products.products_boxes[2].desc_list[0],
          language === "Ru"
            ? "Глубокая аналитика по более 10 параметрам кликов"
            : EngLoc.products.products_boxes[2].desc_list[1],
        ]}
        socials={[Play]}
        href="https://rocketvideo.socialjet.pro/"
      />
      <HorizontalBox
        language={language}
        boxcolor="violet"
        btncolor="#723EDF"
        title={
          language === "Ru"
            ? "Платформа для размещения видео-рекламы на новостных и контентных сайтах"
            : EngLoc.products.products_boxes[3].title
        }
        name="CPA"
        list={[
          language === "Ru"
            ? "Более 300 000 источников во всех социальных сетях и мессенджерах"
            : EngLoc.products.products_boxes[3].desc_list[0],
          language === "Ru"
            ? "Глубокая аналитика по более 10 параметрам кликов"
            : EngLoc.products.products_boxes[3].desc_list[1],
        ]}
        socials={[Telegram, Vk, Instagram, Youtube, Odnoklassniki, Yandex_Zen]}
        href=""
      />
    </SProducts>
  );
};

export default Products;
