const ButtonStyle = css`
  display: flex;
  justify-content: center;
  align-items: center;

  border: none;
  gap: 16px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;

  color: ${({ colorTxt = "#ffffff" }) => colorTxt};
  background: transparent;

  cursor: pointer;
`;

const ButtonBlue = styled.button`
  ${ButtonStyle}
  background: linear-gradient(217.29deg, #0e7ed8 10.81%, #04a2e4 92.27%);
  border-radius: 18px;
  padding: 26px 36px;

  &:active {
    box-shadow: inset 0px 0px 15px 5px #0b65af;
  }
`;

const ButtonSecondary = styled.button`
  ${ButtonStyle}
  padding: 26px 36px;
  width: 100%;
  background: ${({ color }) => color};
  border-radius: 18px;

  &:active {
    box-shadow: inset 0px 0px 15px 5px ${({ color = "#00000052" }) => color};
  }
`;

const ButtonService = styled.button`
  ${ButtonStyle}
  padding: 26px 98px;
  width: ${({ width = "fit-content" }) => width};
  background: ${({ color }) => color};
  border-radius: 18px;

  &:active {
    box-shadow: inset 0px 0px 15px 5px ${({ color = "#00000052" }) => color};
  }
`;

const ButtonGray = styled.button`
  ${ButtonStyle}
`;

const Button = ({
  type = "blue",
  color,
  colorTxt,
  children,
  width,
  link = false,
  handle,
}) => {
  const renderButton = () => {
    switch (type) {
      case "blue":
        return (
          <ButtonBlue>
            {children} {link ? <img width="24" height="24" loading="lazy" src={arrow_up_right}  /> : false}
          </ButtonBlue>
        );
      case "secondary":
        return (
          <ButtonSecondary
            color={color ? color : "#ffffff22"}
            colorTxt={colorTxt}
          >
            {children} {link ? <img width="24" height="24" loading="lazy" src={arrow_up_right} /> : false}
          </ButtonSecondary>
        );
      case "gray":
        return (
          <ButtonGray>
            {children} {link ? <img width="24" height="24" loading="lazy" src={arrow_up_right} /> : false}
          </ButtonGray>
        );
      case "service":
        return (
          <ButtonService
            width={width}
            color={color ? color : "#ffffff22"}
            colorTxt={colorTxt}
          >
            {children}
            {link ? (
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M18 6L6 18M8 6H18V16"
                  stroke="#1073D3"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            ) : (
              false
            )}
          </ButtonService>
        );
    }
  };
  return renderButton();
};

export default Button;
