import styled from "styled-components";
import { Title3, Title4 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";
import { css } from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";

const SHorizontalBox = styled.div`
  grid-column: 1/3;
  width: calc(100% - 120px);
  max-width: 1700px;
  padding: 60px;

  display: flex;
  justify-content: space-between;
  text-align: left;
  gap: 80px;

  border-radius: 53px;
  background: ${({ color }) => {
    switch (color) {
      case "violet":
        return `radial-gradient(
          37.66% 84.16% at 50% 79.68%, 
        rgba(255, 255, 255, 0.2646) 0%, 
          rgba(255, 255, 255, 0) 100%
        ), 
        radial-gradient(
          39.05% 86.62% at 82.28% 50%, 
          rgba(255, 255, 255, 0.26) 0%, 
          rgba(255, 255, 255, 0) 100%
        ), 
        #7A3FF8;`;
      case "pink":
        return `radial-gradient(
          32.01% 129.84% at 35.66% 90.64%, 
          rgba(216, 138, 228, 0.75) 0%, 
          rgba(216, 138, 228, 0) 100%
        ), 
        radial-gradient(
          32.53% 75.25% at 86.53% -6.77%, 
          #CD80DA 0%, 
          rgba(205, 128, 218, 0) 100%
        ), 
        #B138EA;`;
    }
  }};
`;

const ProductView = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: top;
  height: 542px;
  width: 50%;
  padding: 24px;

  background: radial-gradient(
    150.56% 139.78% at -35.42% -28.77%,
    rgba(255, 255, 255, 0.1628) 0%,
    rgba(255, 255, 255, 0.286) 100%
  );
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(13.5px);

  border-radius: 24px;

  & div {
    width: 12px;
    height: 12px;
    margin-right: 17px;
    background: #ffffff;
    opacity: 0.6;
    border-radius: 100%;
  }
`;

const Desc = styled.div`
  max-width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 40px;
`;

const Name = styled(Title3)`
  & span {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    padding: 13px 24px;
    gap: 8px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 38px;
    margin-left: 24px;
    margin-bottom: 40px;
    cursor: pointer;
  }

  & img {
    aspect-ratio: 28 / 28;
  }
`;

const textStyle = css`
  font-weight: 300;
  font-size: 20px;
  line-height: 150%;
`;

const List = styled.ul`
  padding-left: 30px;
  max-width: 530px;
  color: white;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;

  & li {
    position: relative;
    ${textStyle}
  }

  & li::before {
    content: "";
    background-color: #ffffff55; /* Цвет маркера */
    width: 10px;
    height: 10px;
    position: absolute;

    top: 10px;
    left: -26px;
    border-radius: 100%;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  gap: 10px;

  & a {
    width: 100%;
  }
`;

function HorizontalBox({
  boxcolor,
  btncolor,
  name,
  title,
  list,
  socials,
  href,
  language
}) {
  return (
    <SHorizontalBox color={boxcolor}>
      <Desc>
        <Title4>{title}</Title4>
        <div>
          <Name>
            {name}
            <span>
              {socials &&
                socials.map((social) => (
                  <img width="28" height="28" key={Math.random()} loading="lazy" src={social} />
                ))}
            </span>
          </Name>
          <List>
            {list &&
              list.map((list_item) => <li key={Math.random()}>{list_item}</li>)}
          </List>
        </div>
        <ButtonBox>
          <a href={href}>
            <Button type="secondary" color={btncolor}>
              {language === "Ru" ? "Войти" : EngLoc.buttons.login}
            </Button>
          </a>
          <a href={href}>
            <Button type="secondary" link={true}>
              {language === "Ru" ? "Подробнее" : EngLoc.buttons.more}
            </Button>
          </a>
        </ButtonBox>
      </Desc>
      <ProductView>
        <div /> <div /> <div />
      </ProductView>
    </SHorizontalBox>
  );
}

export default HorizontalBox;
